import React from "react";
import "./index.less";
import logo from "../../../assets/image/img_shimingrenzheng.png";
import QRcode from "../../../assets/image/downloadQRcode.png";

class RealAuth extends React.Component<{}> {

  public render() {
    return (
      <div className="mf-real">
        <div className="mf-real__logo">
          <img src={logo} alt=""/>
        </div>
        <div className="mf-real__desc">
          请先在手机客户端完成<br/>
          个人实名认证
        </div>
        <div className="mf-real__qrcode">
          <img src={QRcode} alt=""/>
        </div>
        <div className="mf-real__qrcode__desc">企业萤石云App下载</div>
      </div>
    )
  }

}

export default RealAuth;
